function create_req_type(name: String, readable_name: String):RequirementsType {
    return {"name" : name, "readable_name": readable_name}
}

export const requirements_types = [
    create_req_type("projector", "Проектор"),
    create_req_type("comp", "Компьютер")
]

export class PropertyTable extends React.Component<{data: Array<{name: String, value: any}>}, {}> {
    render(): React.ReactNode {
        return <TableContainer sx={{maxHeight: 300}}>
            <Table sx={{ minWidth: 100 }} stickyHeader aria-label="sticky table" size="small" >
            <TableHead>
                <TableRow>
                <TableCell>Property name</TableCell>
                <TableCell align='left'>Property value</TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {this.props.data.map((row) => (
                <TableRow
                    // key={row.name}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                    <TableCell component="th" scope="row">
                    {row.name}
                    </TableCell>
                    <TableCell align="left">{row.value}</TableCell>
                </TableRow>
                ))}
            </TableBody>
            </Table>
        </TableContainer>
    }
}



export class FindRoomsResults extends React.Component<{data: Array<{id: String}>, clickClb: (name: String) => void, selected_room: String}, {}> {
    render(): React.ReactNode {
        return (
            <TableContainer sx={{maxHeight: 300}}>
            <Table sx={{ minWidth: 100 }} stickyHeader aria-label="sticky table" size="small" >
            <TableHead>
                <TableRow>
                <TableCell>Property name</TableCell>
                <TableCell align='right'> </TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {this.props.data.map((row) => (
                <TableRow
                hover
                selected= {(this.props.selected_room == row.id)}
                // onClick={() => {
                //     this.props.clickClb(row.id)
                // }}
                // color=
                    sx={{ '&:last-child td, &:last-child th': { border: 0 }}}
                >
                    <TableCell component="th" scope="row">  
                    {row.id}
                    </TableCell>
                    <TableCell  align="right"><Button onClick={() => {
                        this.props.clickClb(row.id)
                    }}> show</Button></TableCell>
                </TableRow>
                ))}
            </TableBody>
            </Table>
        </TableContainer>
        )
    }
}

export class FindRoomsRequirementsMask extends React.Component<{requirements_types: Array<RequirementsType>}, {mask: Map<String, boolean>}> {
    constructor(params) {
        super(params)
        this.state = {
            mask: new Map<String, boolean>()
        }
        for (const i in this.props.requirements_types) {
            this.state.mask.set(this.props.requirements_types[i].name,  false)
        }
    }
    chip_click(name) {
        var a = this.state.mask;
        a.set(name, !a.get(name))
        this.setState({mask: a})
        
    }
    render() {
        console.log("FindRoomsRequirementsMask Redraw")
        return (
        <ul 
                    style={{
                        display: 'flex',
                        justifyContent: 'left',
                        flexWrap: 'wrap',
                        listStyle: 'none',
                        padding: 0.0,
                        margin: 0
                    }}
                    // component="ul"
                    >
            {/* <Stack direction="row" spacing={1}> */}
                {this.props.requirements_types.map((e) => ( 
                    <ListItemLocal>
                        <Chip variant="outlined"  onClick={()=>{this.chip_click(e.name)}}  label={e.readable_name} color={this.state.mask.get(e.name) ? "primary" : "default"} />
                    </ListItemLocal>
                ))}
            {/* </Stack> */}
            </ul>
        )
        
    }
}

