// @ts-nocheck

import * as THREE from 'three'

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { OrbitControls } from 'three/addons/controls/OrbitControls';
import Stats from 'three/addons/libs/stats.module.js';

import { EffectComposer } from 'three/addons/postprocessing/EffectComposer.js';
import { SSAOPass } from 'three/addons/postprocessing/SSAOPass.js';



class ThreeViewer {
    renderer: THREE.WebGLRenderer
    camera: THREE.PerspectiveCamera
    scene: THREE.Scene
    element: HTMLDivElement
    controls: OrbitControls
    cube: THREE.Mesh
    stats: Stats
    composer: EffectComposer
    selected_room:  String | null
    raycaster: THREE.Raycaster
    rooms: Array<{
        name: String,
        object: THREE.Object3D
    }> | null
    rooms_map: Map<String, number> | null
    pointer = new THREE.Vector2()

    pnt_down_t: number = 0;
    pnt_ev: MouseEvent | null = null;
    onselect: CallableFunction | null = null;
    onload: CallableFunction | null = null;

    constructor(element: HTMLDivElement) {
        this.element = element
        this.rooms = null
        this.rooms_map = null
        this.selected_room = null
    }
    load_model(): void {
        const alpha_mat = new THREE.MeshPhongMaterial({ color: 0x000000, transparent: false, opacity: 0, alphaTest: 1, "name": "PH_Стекло_Окно.001" })
        const rama_mat = new THREE.MeshPhongMaterial({ "color": new THREE.Color(0.4, 0.4, 0.4), "name": "PH_Дерево_Рама.001" })
        const room_mat = new THREE.MeshPhongMaterial({ "color": new THREE.Color(0.3, 0.01, 0.7), "opacity": 0.4, "transparent": true })
        const default_mat = new THREE.MeshPhongMaterial({ "color": new THREE.Color(1, 1, 1) })

        const loader = new GLTFLoader();
        loader.load("/models/nic_1.glb", (gltf) => {
            this.rooms = new Array<{
                name: String,
                object: THREE.Object3D
            }>();

            this.rooms_map = new Map<String, number>();
            console.log("gltf:", gltf)
            // @ts-nocheck 
            gltf.scene.traverse((child)=> {

                // @ts-nocheck 
                if (child.isMesh) {
                    // child.castShadow = true;
                    // child.receiveShadow = true;
                    if ("material" in child) {
                        if (child.material.name == "PH_Дерево_Рама.001") {
                            child.material = rama_mat
                            child.castShadow = true;
                            child.receiveShadow = true;
                        }
                        else if (child.material.name == "PH_Стекло_Окно.001" || child.material.name == "PH_Остекление.001") {
                            child.material = alpha_mat;
                            child.visible = false;
                        }
                        else if (child.name.indexOf("IfcSpace") == 0) {
                            child.material = room_mat
                            child.visible = false;
                            child.layers.enable(2)
                            this.rooms?.push({name:child.name, object:child})
                            this.rooms_map?.set(child.name, this.rooms.length - 1)
                        }
                        else {
                            child.material = default_mat

                            child.castShadow = true;
                            child.receiveShadow = true;
                        }
                    }
                }
            })
            this.scene.add(gltf.scene)
            console.log(this.rooms)
            console.log(this.rooms_map)
            
            if (this.onload != null) {
                this.onload()
            }
        })

    }
    setup(): void {
        this.renderer = new THREE.WebGLRenderer({antialias: true, alpha: true, logarithmicDepthBuffer: true, powerPreference: "low-power"});
        this.renderer.setSize( window.innerWidth, window.innerHeight );
        this.renderer.setPixelRatio( window.devicePixelRatio );

        this.element.appendChild( this.renderer.domElement );

        // enable shadows
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;

        this.scene = new THREE.Scene();
        this.camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 );
    
        this.controls = new OrbitControls( this.camera, this.renderer.domElement );
        this.camera.position.x = 70
        this.camera.position.y = 48
        this.camera.position.z = -66
        this.controls.target.x = 58
        this.controls.target.y = -6
        this.controls.target.z = -72
        console.log(this.controls)




        const light: THREE.DirectionalLight = new THREE.DirectionalLight( 0xffffff, 1);
        light.position.set( 50, 59, 100 ); //default; light shining from top
        light.castShadow = true; // default false
        this.scene.add( light );

        //Set up shadow properties for the light
        light.shadow.mapSize.width = 512; // default
        light.shadow.mapSize.height = 512; // default
        light.shadow.camera.near = 0.5; // default
        light.shadow.camera.far = 500; // default
        light.shadow.camera.left = -100;
        light.shadow.camera.right = 150;
        light.shadow.camera.top = 100;
        light.shadow.camera.bottom = -100;
        light.shadow.mapSize.width = 1024*10;
        light.shadow.mapSize.height = 1024*10;

        const alight = new THREE.AmbientLight( 0xffffff, 0.3 ); // soft white light
        this.scene.add( alight );

        const hlight = new THREE.HemisphereLight( new THREE.Color().setHSL(0.6, 0.5, 0.6), new THREE.Color().setHSL(.095, 0.5, 0.75), 0.3 );
        this.scene.add( hlight );

        this.stats = new Stats();
		// this.element.appendChild( this.stats.dom );


        // this.composer = new EffectComposer( this.renderer );
        // const width = window.innerWidth;
        // const height = window.innerHeight;
        // const ssaoPass = new SSAOPass( this.scene, this.camera, width, height );
        // ssaoPass.radius = 11;
        // // ssaoPass.samples = 5;
        // ssaoPass.minDistance = 0.005;
        // ssaoPass.maxDistance = 1;
        // ssaoPass.intensity = 10;
        // ssaoPass.output = SSAOPass.OUTPUT.Blur;
        // this.composer.addPass( ssaoPass );
        
        this.raycaster = new THREE.Raycaster();
        this.raycaster.layers.set(2)

        if (this.element != null) {
            this.element.onpointerdown = (ev) => {
                if (ev.button == 0) {
                    this.pnt_down_t = Date.now()
                    this.pnt_ev = ev
                    setTimeout(()=>{
                        this.pnt_down_t = 0
                    }, 300)
                }
            }
            this.element.onpointerup = (ev) => {
                if (this.pnt_down_t != 0 && this.pnt_ev != null && ev.button == 0) {
                    if ((Date.now() - this.pnt_down_t) < 300 && Math.sqrt((ev.clientX - this.pnt_ev.clientX)**2 + (ev.clientY - this.pnt_ev.clientY)**2) <30 ) {
                        this.click(ev)
                    }
                    this.pnt_down_t = 0
                }
            }
        }
    }
    
    click(ev) {
        this.pointer.x = ( ev.clientX / window.innerWidth ) * 2 - 1;
	    this.pointer.y = - ( ev.clientY / window.innerHeight ) * 2 + 1;
        // update the picking ray with the camera and pointer position
        this.raycaster.setFromCamera( this.pointer, this.camera );

        // calculate objects intersecting the picking ray
        const intersects = this.raycaster.intersectObjects( this.scene.children );
        console.log(intersects)
        // if (intersects.length > 0)
        //     if (intersects[0].object.name != this.selected_room)
        //         this.setSelectedRoom(intersects[0].object.name)
        //     else
        //         this.setSelectedRoom(null)
        // else 
        //     this.setSelectedRoom(null)
        
        // if (this.onselect != null ) {\
        if (intersects.length > 0)
            if (intersects[0].object.name != this.selected_room)
                this.onselect(intersects[0].object.name)
            else
                this.onselect(null)
        else
            this.onselect(null)
        // }
        
    }
    geometry(): void {
        const geometry = new THREE.BoxGeometry( 1, 1, 1 );
        const material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );
        this.cube = new THREE.Mesh( geometry, material );
        this.scene.add( this.cube );
        this.load_model()
    }
    animate() {
        // console.log("this:", this)
        requestAnimationFrame( () =>{this.animate()} );

        this.controls.update();

        this.stats.begin();
        // this.composer.render( );
        this.renderer.render( this.scene, this.camera );
        this.stats.end();
    }
    changeColor(): void {
        console.log("changeColor")
        this.cube.material.color.set( 0xff0000 );
    }
    setSelectedRoom(new_room: String | null): void {
        if (this.selected_room != new_room && this.rooms_map != null && this.rooms != null) {
            if (this.selected_room != null){
                const i = this.rooms_map.get(this.selected_room);
                this.rooms[i].object.visible = false;
            }
            
            if (new_room != null && this.rooms_map.has(new_room)) {
                const i = this.rooms_map.get(new_room);
                this.rooms[i].object.visible = true;
                // console.log("this.rooms[i].object.position", this.rooms[i].object.)
                // this.controls.target.set(this.rooms[i].object.position)
                this.selected_room = new_room
            }
            else {
                this.selected_room = null
            }
        }
        console.log("this.selected_room", this.selected_room)
        console.log("orbitalctrl", this.controls)
    }
    entry(): void {
        this.setup()
        this.geometry()

        this.animate();
    }
}


export default ThreeViewer;