import * as React from 'react';
import * as OC from './OverlayComponents'

import {useRef} from "react";

import {ThemeProvider, createTheme } from '@mui/material/styles';

import ThreeViewer from './ThreeViewer';

const THEME = createTheme({
    typography: {
    //   "fontFamily": "\"MyCustomFont\"",
    //   "fontSize": 20,
    //   "lineHeight": 1.5,
    //   "letterSpacing": 0.32,
    //   useNextVariants: true,
    //   suppressDeprecationWarnings: true,
      h4: {
        "fontWeight": 200,
      },
      h3: {
        "fontWeight": 200,
      },
      h6: {
        "fontWeight": 200,
      }
    },
  });

export default class App extends React.Component<{}, {selected_room_id: String}> {
    rooms: Map<String, OC.RoomData>
    three_root_element: HTMLDivElement | null;
    three_viewer: ThreeViewer | null;

    find_rooms_ref: React.RefObject<OC.FindRooms>
    
    constructor(params) {
        super(params)
        this.find_rooms_ref = React.createRef<OC.FindRooms>();
        this.state = {
            selected_room_id: "123",
        }
        
        this.rooms = new Map<String, OC.RoomData>([
            [OC.room.id, OC.room],
            [OC.room2.id, OC.room2]
        ]);
        
    }
    
    componentDidMount(): void {
        if (this.three_root_element) {
            this.three_viewer = new ThreeViewer(this.three_root_element);
            this.three_viewer.onselect = (s: String|null) => {
                if (s == null)
                    this.setState({
                        selected_room_id: null
                    })
                else {
                    const a = s.replace("IfcSpace", "")
                    if (a == this.state.selected_room_id)
                        this.setState({
                            selected_room_id: null
                        })
                    else
                        this.setState(
                            {selected_room_id: a}
                        )
                }
            }
            this.three_viewer.onload = () => {
                const a = Array.from(this.three_viewer.rooms_map.keys()).map((s)=>({id: s.replace("IfcSpace", "")}))
                console.log("onload", a)
                this.find_rooms_ref.current.setState({
                    rooms_found: a
                })
            }
            this.three_viewer.entry();
        }
        else {
            console.error("three_root_element is null");
        }
    }
    
    componentDidUpdate(prevProps: Readonly<{}>, prevState: Readonly<{selected_room_id: String}>, snapshot?: any): void {
        if (this.state.selected_room_id != prevState.selected_room_id) {
            console.log("send selection")
            if (this.three_viewer) {
                this.three_viewer.changeColor();
                this.three_viewer.setSelectedRoom("IfcSpace" + this.state.selected_room_id)
            }
        }
    }
    render() {
        return (
            <>
                <ThemeProvider theme={THEME}>
                    <div className='overlay' style={{width: "25em"}}>
                        <OC.FindRooms selected_room={this.state.selected_room_id} ref={this.find_rooms_ref} show_clk_callback={(room_name) => {
                            this.setState({
                                selected_room_id: room_name
                            })
                        }}/>    
                        {this.rooms.has(this.state.selected_room_id) ? (<OC.RoomInfo room_data = {this.rooms.get(this.state.selected_room_id)} />) : <OC.RoomInfo room_data = {{id: this.state.selected_room_id, readable_name: this.state.selected_room_id, properties: [], items: []}} />}
                        
                        <input onChange={(e)=>{this.setState({selected_room_id:e.target.value})}} defaultValue="123" type="text"></input>
                        
                    </div>
                </ThemeProvider>
                <div className='canvas' ref={element => this.three_root_element = element} />
            </>
            )
    }
}

